@mixin card($background, $padding, $shadow: none) {
  border-radius: 24px;
  // width: $width;
  box-shadow: $shadow;
  padding: $padding;
  background: $background;
}

@mixin button($width, $height, $padding) {
  width: $width;
  height: $height;

  padding: $padding;

  background: $color-red;
}

@mixin container($padding-left, $padding-right) {
  margin-left: auto;
  margin-right: auto;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: $padding-left;
  padding-right: $padding-right;

  // outline: 1px solid tomato;
}

@mixin bg-image($url) {
  background-image: url($url),
    linear-gradient(to right, $color-pink-dark, $color-pink);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
