.work_plan {
  background-image: linear-gradient(
    to bottom,
    $color-white 7%,
    $color-pink-light 7%,
  );
  @media screen and (min-width: $media-mob-middle) {
    padding-bottom: 77px;
    background-image: linear-gradient(
      to bottom,
      $color-white 8%,
      $color-pink-light 8%,
    );
  }
  @media screen and (min-width: $media-tab) {
    padding-bottom: 81px;
    background-image: linear-gradient(
      to bottom,
      $color-white 15%,
      $color-pink-light 15%,
    );
  }
  @media screen and (min-width: $media-desctop) {
    padding-bottom: 86px;
    background-image:
    url('../images/work_plan/desctop/wp_bg.webp'),
    url("../images/work_plan/desctop/wp_bg.png"),
    linear-gradient(
    to bottom,
    $color-white 15%,
    $color-pink-light 15%,
    );
    width: 100%;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 0 30%;
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
          background-image:
            url('../images/work_plan/desctop/wp_bg@2x.webp'),
            url('../images/work_plan/desctop/wp_bg@2x.png'),
            linear-gradient(
            to bottom,
            $color-white 15%,
            $color-pink-light 15%,
            );
      }
    @media screen and (min-device-pixel-ratio: 3),
        screen and (min-resolution: 3dppx),
        screen and (min-resolution: 288dpi) {
          background-image:
            url('../images/work_plan/desctop/wp__bg@3x.webp'),
            url('../images/work_plan/desctop/wp_bg@3x.png'),
            linear-gradient(
            to bottom,
            $color-white 15%,
            $color-pink-light 15%,
            );
        }
    }
}


.work-plan__container {
  @include container(20px, 20px);
  padding-bottom: 54px;
  width: 320px;
  @media screen and (min-width: $media-mob-middle) {
    @include container(28px, 28px);
    width: 576px;
  }
  @media screen and (min-width: $media-tab) {
    @include container(36px, 36px);
    width: 768px;
  }
  @media screen and (min-width: $media-desctop) {
    @include container(115px, 115px);
    width: 1280px;
  }
}

.work-plan__list {
  @extend %list;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: $media-mob-middle) {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  @media screen and (min-width: $media-tab) {
    flex-wrap: nowrap;
    margin-left: -10px;
    margin-right: -10px;
  }
  @media screen and (min-width: $media-desctop) {
    margin-left: -15px;
    margin-right: -15px;
  }
}

.work-plan__card {
  @include card($color-white, 43px 20px, $shadow: $card-shadow);
  margin-left: 30px;
  margin-right: 30px;
  width: calc((79%-2*30px)/1);
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  @media screen and (min-width: $media-mob-middle) {
    margin-left: 25px;
    margin-right: 25px;
    width: calc((80%-4*25px)/2);
  }
  @media screen and (min-width: $media-tab) {
    margin-left: 10px;
    margin-right: 10px;
    width: calc((100%-6*10px)/3);
    padding: 50px 31px;
    font-size: 14px;
  }
  @media screen and (min-width: $media-desctop) {
    margin-left: 15px;
    margin-right: 15px;
    width: calc((100%-6*15px)/3);
    font-size: 16px;
  }
  &:not(:last-child) {
    @media screen and (max-width: 575px) {
      margin-bottom: 20px;
    }
    @media screen and (min-width: $media-mob-middle) {
      margin-bottom: 35px;
    }
    @media screen and (min-width: $media-tab) {
      margin-bottom: unset;
    }
  }
}

.work-plan__card-name {
  width: max-content;
  padding-left: 25px;
  padding-right: 25px;
  margin-bottom: 25px;
  text-align: center;
  font-family: "DM Sans", sans-serif;
  letter-spacing: 0.04em;
  color: $color-white;
  background-color: $color-orange;
  border-radius: 4px;
  @media screen and (min-width: $media-tab) {
    margin-bottom: 30px;
    padding-left: 16px;
    padding-right: 16px;
  }
  @media screen and (min-width: $media-desctop) {
    margin-bottom: 32px;
  }
}

.work-plan__card-name.accent {
  background-color: $color-green;
}

.work-plan__place {
  margin-bottom: 10px;
  font-family: "DM Sans", sans-serif;
  text-transform: none;
  font-weight: 500;
  letter-spacing: 0.04em;
  color: $color-black;
  @media screen and (min-width: $media-tab) {
    margin-bottom: 14px;
  }
  @media screen and (min-width: $media-desctop) {
    margin-bottom: 10px;
  }
}

.work-plan__description {
  margin: 0;
  line-height: 1.5;
  color: $color-gray;
  @media screen and (min-width: $media-desctop) {
    letter-spacing: 0.04em;
    line-height: 1.83;
  }
}

.work-plan__line {
  padding-bottom: 25px;
  border-bottom: 1px solid #E1E1E1;
  @media screen and (min-width: $media-tab) {
    padding-bottom: 30px;
  }
}

.work-place__schedule {
  display: inline-flex;
  flex-direction: column;
  margin-top: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid #E1E1E1;
  color: $color-black;
  @media screen and (min-width: $media-tab) {
    margin-top: 30px;
    padding-bottom: 30px;
  }
}

.schedule__time {
  color: $color-pink;
  &:not(:last-child) {
    @media screen and (min-width: $media-mob) {
      margin-bottom: 10px;
    }
    @media screen and (min-width: $media-tab) {
      margin-bottom: 16px;
    }
  }
}

.work-plan__contacts {
  margin-top: 25px;
  @media screen and (min-width: $media-tab) {
    margin-top: 30px;
  }
}

.work-plan__link {
  @extend %link;
  padding-bottom: 2px;
  padding-top: 2px;
  font-size: 12px;
  color: $color-gray;
  @media screen and (min-width: $media-tab) {
    font-size: 14px;
    line-height: 1.86;
  }
  @media screen and (min-width: $media-desctop) {
    font-size: 16px;
    line-height: 1.83;
  }
}
